import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { AppDispatch, AppState } from '../index'
import { roladPage } from './actions'

export function useIceState(): AppState['Ice'] {
  return useSelector<AppState, AppState['Ice']>((state) => state.Ice)
}

export function useIceHandlers(): {
  onIsRefresh:(isRefresh:boolean)=>void
} {
  const dispatch = useDispatch<AppDispatch>()
  const onIsRefresh = useCallback((isRefresh:boolean) => {
    dispatch(roladPage({isRefresh}))
  }, [dispatch])

  return { onIsRefresh }
}
